import { IgcChildContentModule } from 'igniteui-core/igc-child-content-module';
import { IgcPopupComponent } from './igc-popup-component';
import { Popup } from './Popup';
import { TypeRegistrar } from 'igniteui-core/type';


export class IgcPopupModule {
    public static register() {
                IgcChildContentModule.register();
        IgcPopupComponent.register();
        TypeRegistrar.registerCons('IgcPopupComponent', IgcPopupComponent);        TypeRegistrar.registerCons('IgcPopupComponent', IgcPopupComponent);
        TypeRegistrar.register('Popup', (Popup as any).$type);

    }
}