import { Base, Type, markType } from "igniteui-core/type";
import { DomWrapper, DomWrapper_$type, NormalizedEvent } from "igniteui-core/dom";
import { ModifierKeys } from "igniteui-core/ModifierKeys";

/**
 * @hidden 
 */
export class ExpansionPanelUtilities extends Base {
	static $t: Type = markType(ExpansionPanelUtilities, 'ExpansionPanelUtilities');
	static a(a: DomWrapper): boolean {
		let b = <HTMLInputElement>a.getNativeElement();
		return b.checked;
	}
	static b(a: DomWrapper, b: boolean): void {
		let c = <HTMLInputElement>a.getNativeElement();
		c.checked = b;
	}
	static c(a: NormalizedEvent): ModifierKeys {
		let b = <ModifierKeys>0;
		if (a.altKey) {
			b |= 1;
		}
		if (a.shiftKey) {
			b |= 4;
		}
		if (a.ctrlKey) {
			b |= 2;
		}
		return b;
	}
}


